/* jshint ignore:start */

if (!runningTests) {
  require("peepchat/app")["default"].create({"name":"peepchat","version":"0.0.0+ed05f761"});
}

define('~fastboot/app-factory', ['peepchat/app', 'peepchat/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


/* jshint ignore:end */
